@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
:root {
    --border-radius: 3px;
    --primary-color: rgb(0, 120, 210);
}

body {
    background-color: #DAE0E6;
    margin: 0;
    padding: 0;
}

.hoverable:hover {
    cursor: pointer;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }

.content{
    width: 1080px;
    margin: auto;
    margin-top: 20px;
}

.bars-wrapper {
    display: flex;
    margin: 0px 5px;
    margin-top: 15px;
}
.title {
    font-size: 18px;
    font-weight: 600;
    margin-left: 10px;
    margin-bottom: 10px;
}
.pagination-container {
    margin: 5px 5px 50px 5px;
    display: flex;
    justify-content: center;
  }

.button-container{
  display: flex;
  align-items: center;
}

ul {
  list-style-type: none;
}
  
.right-pagination-button {
  float: right;
  margin: 0 2px 0 10px;
  border: solid 1px #CCCCCC;
  border-radius: 3px;
  background-color: white;
  padding: 5px 8px 5px 8px;
}

.right-pagination-button:hover{
  background-color: #F6F7F8;
  border-radius: 5px;
  cursor: pointer;
}

.left-pagination-button {
  float: right;
  margin: 0 10px 0 5px;
  border: solid 1px #CCCCCC;
  border-radius: 3px;
  background-color: white;
  padding: 5px 8px 5px 8px;

}

.left-pagination-button:hover{
background-color: #F6F7F8;
border-radius: 5px;
cursor: pointer;
}

.btn {
    padding: 5px 30px;
    border: 1.5px solid var(--primary-color);
    border-radius: 25px;
    font-weight: 500;
    min-width: 100px;
}

.btn:hover{

    cursor: pointer;
}

/* Button Colors & Styles */

.btn--primary{
    background-color: var(--primary-color);
    color: white;
    
}

.btn--primary:hover{
    background-color: rgb(125, 190, 255);
    border-color: 1px solid rgb(125, 190, 255);

}

.btn--secondary {
    color: var(--primary-color);
    background-color: white;
}

.btn--secondary:hover {
    color: var(--primary-color);
    background-color: rgb(248,248,248)
}

/* Button Sizes */

.btn--small {
    padding: 7px 20px 7px 20px;
    font-size: 14px;
}

.btn--medium {
    padding: 10px 20px;
    font-size: 18px;
}

.btn--large {
    padding: 15px 30px;
    font-size: 20px;
}


form {
    margin: auto;
}

.form-group{
    width: 100%;
    max-width: 100%;
    padding: 5px 0;
    position: relative;
}

.form-control{
    background-color: #E8F0FE;
    border: 1px solid #24A0ED;
    margin-top: 5px;
    border-radius: 3px;
    padding-left: 10px;
    width: 100%;
    padding: 10px 0;
    display: block;
}


.form-label{
    color: black;
    font-size: 16px;
}


.login-button{
    margin-top: 10px;
    text-align: center;
    padding: 10px;
}
.overlay-style{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .7);
    z-index: 100;
}

.modal-style{
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: #FFF;
    border-radius: 5px;
    z-index: 100;
}

.modal-container{
    margin: 155px 250px 155px 250px;
    display: flex;
}

.exit-button{
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
}

.modal-side-bar{
    background-color: #BFE4FC;
    border-left: 5px;
    position: absolute;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 130px;
}

.modal-login{
    width: 250px;
    margin-left: 50px;
}

.modal-link{
    color: #24A0ED;
}

.modal-link:hover{
    cursor: pointer;
}
