    border: 1px solid rgb(230, 230, 230);
    background-color: rgb(245, 245, 245);
}

.searchBar input:hover {
    border-color: var(--primary-color);
    background-color: white;
    
}


.searchBar label {
    position: absolute;
    display: flex;
    align-items: center;
    height: 100%;
    margin-left: 15px;
}

.searchBar label svg {
    color: rgb(130, 130, 130)
}


